<script>
export default {
  name: "menuVueStudyPage",
  data : ()=> ({
    message : "",
    preview : '',
    method1Show : false,
    method2Show : false,
    method3Show : false,
    method4Show : false,
    method6Show : false,
    method7Show : false,
