        "  'name' => 'hoyoung3',\n" +
        "  {} => 'hoyoung4',\n" +
        "  true => '',\n" +
        "  1 => '1',\n" +
        "  '1' => '1'\n" +
        "}",
    method2Result : "1\n" +
        "1\n" +
        "undefined\n" +
        "undefined",
    method3Result : "true\n" +
        "true\n" +
        "false\n" +
        "false",
