      "delete()":"Map(4) {\n" +
          "  'name' => 'hoyoung1',\n" +
          "  {} => 'hoyoung4',\n" +
          "  true => '',\n" +
          "  '1' => '1'\n" +
          "}",
      "keys()":"[Map Iterator] { 'name', {}, true, '1' }",
      "values()":"[Map Iterator] { 'hoyoung1', 'hoyoung4', '', '1' }",
      "size":"4",
      "clear()":"Map(0) {}"
    },
    method6Result : {
      "add(2) ": " Set(2) { 1, 2 }" ,
      "add() ":" Set(3) { 1, 2, undefined }" ,
      "setT() ": " Set(3) { 1, 2, undefined }",
      "setT1() ": "  Set(4) { 4, 1, 2, 3 }"
    },
    method7Result : {
      "setT1()":"Set(3) { 1, 2, 3 }",
      "setT1.delete(4)":"false",
      "setT1.delete(3)":"true",
    },
    method8Result : {
      "has(4)":"true",
      "has('4')":"false",
      "has(6)":"false ",
      "setT1.size":"4",
      "setT1(clear() )":"Set(0) {}",

    }

  }),
  methods : {
    handleChange :function (event){
      let file = event.target.files[0]
      if(file && file.type.match(/^image\/(png|jpeg)$/)){
        this.preview = window.URL.createObjectURL(file)
      }
    },
    shClick(event){
      let ele = event.target;

      ele.classList.toggle('btn_toggle');
      ele.classList.toggle('btn_success');

      if(ele.classList.contains("btn_toggle")){
        ele.innerHTML = "";
      }else{
        ele.innerHTML = "";
      }
      ele.parentNode.parentNode.children[1].classList.toggle('study_none');
      //contains       
      //class   ( jqurey $(this).togglSwitch )

    },
    changeCode(str){
      console.log(str);

      return "<div>1</div>"
    },
    //      
    // getElementIndex(ele){
    //   //   ?
    //   //  
    //   // console.log(Array.from(ele.parentNode.childNodes).indexOf(ele))
    //   return [].indexOf.call(ele.parentNode.children, ele);
    // },
    method1(){
      console.log("-------MAP ,set -------");
      let mapT = new Map();
      let name = {};
      mapT.set("name","hoyoung1")
      mapT.set("name","hoyoung2")
      mapT.set("name","hoyoung3")
      mapT.set(name,"hoyoung4")
      mapT.set(true,"");
      mapT.set(1,"1");
      mapT.set("1","1");
      console.log(mapT)
    },
    method2(){
      console.log("-------MAP ,get -------");
      let mapT = new Map();
      let name = {};
      mapT.set("name","hoyoung1")
      mapT.set(name,"hoyoung4")
      mapT.set(true,"");
      mapT.set(1,"1");
      mapT.set("1","1");
      mapT.set(new Object,"1")


      console.log(mapT.get(1))
      console.log(mapT.get("1"))
      console.log(mapT.get(false)) //   x
      console.log(mapT.get(new Object()))  //   x
    },
    method3(){
      console.log("-------MAP has -------");
      let mapT = new Map();
      let name = {};
      mapT.set("name","hoyoung1")
      mapT.set(name,"hoyoung4")
      mapT.set(true,"");
      mapT.set(1,"1");
      mapT.set("1","1");
      mapT.set(new Object,"1")


      console.log(mapT.has(1))
      console.log(mapT.has("1"))
      console.log(mapT.has(false)) //   x
      console.log(mapT.has(new Object()))  //   x

    },
    method4(){
      console.log("-------MAP keys,values,size,clear -------");
