      console.log("values : "+mapT.values());
      console.log("size : "+mapT.size);
      mapT.clear()
      console.log("clear : "+mapT);
    },
    method5(){
      console.log("-------MAP >map, >map , map> -------");
      // map
      let map = new Map([
        ['1',  'str1'],
        [1,    'num1'],
        [true, 'bool1']
      ]);

      console.log( map.get('1') ); // => str1

      // map
      let obj = {
        name: "hoyoung",
        age: 25
      };

      let map1 = new Map(Object.entries(obj));

      console.log( map1.get('name') ); // => hoyoung


      //map 
      let obj1 = Object.fromEntries(map1.entries())
      console.log(obj1);
      //{
      //  name: "hoyoung",
      //  age: 25
      //};
    },
    method6(){
      // Set  add  
      console.log("-------SET ,add -------");
      let setT = new Set();
      setT.add(1);
      setT.add(1);
      setT.add(2);
      setT.add(2);
      console.log(setT.add(2));
      console.log(setT.add());
      console.log(setT);

      //   
      let arr = [4,4,4,1,1,1,2,2,2,3,3,3]; // 1~4  
      let setT1 = new Set(arr);
      console.log(setT1);
    },
    method7(){
      console.log("-------SET delete -------");
      //   
      let arr = [4,4,4,1,1,1,2,2,2,3,3,3]; // 1~4  
      let setT1 = new Set(arr);
      // Set(4) { 4, 1, 2, 3 }
